import React, { Component } from "react";
import "../../style.css";
import NumbersAPI from "../../Component/NumbersAPI/NApi";
import CompoundInterest from "../../Component/calculatorSecond/calculatorsecond";
import SecondAPI from "../../Component/SecondAPI/SecondAPI";
import TATA from "../Home/images/TATA Asset Management Company/1.png";
import TATAF1 from "../Home/images/TATA Asset Management Company/Personalized Approach.svg";
import TATAF2 from "../Home/images/TATA Asset Management Company/Risk Assessment and Management.svg";
import TATAF3 from "../Home/images/TATA Asset Management Company/Portfolio Tracking.svg";
import { withTranslation } from "react-i18next";
import {Helmet} from 'react-helmet';

export class pms extends Component {
  constructor() {
    super();
  }

  data = {
    TATA: {
      Title: "",
      ProductCategoryId: "3",
      SebiCategory: "",
      SebiSubCategory: "",
      DefaultProductId: "1",
    },
  };

  render() {
    const productinfo = this.data.TATA;
    const { t } = this.props;

    return (
      <section>
        <Helmet>
                <meta charSet="utf-8" />
                <title>{t('MetaTitle:mT68')}</title>
                <meta name="description" content={t('MetaDescription:mD68')} />
                <link rel="canonical" href={window.location.href} />
            </Helmet>
        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-7 col-md-6 mb-4">
              <h2>{t("TATA:IOITAMC1")}</h2>
              <p>{t("TATA:IOITAMC2")}</p>
            </div>
            <div className="col-lg-5 col-md-6">
              <img src={TATA} className="img-fluid" />
            </div>
          </div>
        </div>
        <div className="API-Section">
          <div className="container container-wrapper">
            <h2 className="text-center">{t("TATA:IOITAMC3")}</h2>
            <div className="row row justify-content-lg-center">
              <div className="col col-lg-10">
                <SecondAPI
                  productCategoryId={productinfo.ProductCategoryId}
                  sebiCategory={productinfo.SebiCategory}
                  sebiSubCategory={productinfo.SebiSubCategory}
                  defaultProductId={productinfo.DefaultProductId}
                />
              </div>
            </div>
          </div>
        </div>

        <div className="bg-lightgrey">
          <div className="container container-wrapper px-4">
            <h2 className="text-center">{t("TATA:IOITAMC4")}</h2>
            <div className="row row justify-content-center gx-5">
              <div className="col-lg-6 mb-4">
                <div className="p-3 bg-white h-100">
                  <div className="col-lg-3">
                    <img src={TATAF1} className="img-fluid" />
                  </div>
                  <div className="col-lg-9">
                    <h4>{t("TATA:IOITAMC5")}</h4>
                    <p className="small-text">{t("TATA:IOITAMC6")}</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 mb-4">
                <div className="p-3 bg-white h-100">
                  <div className="col-lg-3">
                    <img src={TATAF2} className="img-fluid" />
                  </div>
                  <div className="col-lg-9">
                    <h4>{t("TATA:IOITAMC7")}</h4>
                    <p className="small-text">{t("TATA:IOITAMC8")}</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 mb-4">
                <div className="p-3 bg-white h-100">
                  <div className="col-lg-3">
                    <img src={TATAF3} className="img-fluid" />
                  </div>
                  <div className="col-lg-9">
                    <h4>{t("TATA:IOITAMC9")}</h4>
                    <p className="small-text">
                      Tata AMC PMS continuously tracks all the investments and
                      in the portfolio. It helps the portfolio manager and the
                      client to see how the investments are lined up against the
                      goals and compare how the portfolio is faring against the
                      market.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="container container-wrapper">
          <h2 className="text-center">{t("TATA:IOITAMC10")}</h2>
          <div className="row gx-5">
            <div className="col-lg-6 col-md-6 mb-4">
              <div className=" p-3 bg-lightblue h-100">
                <h4>{t("TATA:IOITAMC11")}</h4>
                <ul className="small-text">
                  <li>{t("TATA:IOITAMC12")}</li>
                  <li>{t("TATA:IOITAMC13")}</li>
                  <li> {t("TATA:IOITAMC14")}</li>
                  <li>{t("TATA:IOITAMC15")}</li>
                  <li>{t("TATA:IOITAMC16")}</li>
                  <li>{t("TATA:IOITAMC17")}</li>
                  <li>{t("TATA:IOITAMC18")}</li>
                  <li>{t("TATA:IOITAMC19")}</li>
                </ul>
              </div>
            </div>
            <div className="col-lg-6 col-md-6 mb-4">
              <div className="p-3 bg-lightblue h-100">
                <h4>{t("TATA:IOITAMC20")}</h4>
                <ul className="small-text">
                  <li>{t("TATA:IOITAMC21")}</li>
                  <li>{t("TATA:IOITAMC22")}</li>
                  <li>{t("TATA:IOITAMC23")}</li>
                  <li>{t("TATA:IOITAMC24")}</li>
                  <li>{t("TATA:IOITAMC25")}</li>
                  <li>{t("TATA:IOITAMC26")}</li>
                  <li>{t("TATA:IOITAMC27")}</li>
                  <li>{t("TATA:IOITAMC28")}</li>
                  <li>{t("TATA:IOITAMC29")}</li>
                  <li> {t("TATA:IOITAMC30")}</li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        <div className="landingPlatforms">
          <div className="container">
            <div className="row">
              <h2>{t("TATA:IOITAMC31")}</h2>
              <p>
                {t("TATA:IOITAMC32")}
                <br />
                <br />
                {t("TATA:IOITAMC33")}
                <br />
                <br />
                {t("TATA:IOITAMC34")}
                <br />
                <br />
                {t("TATA:IOITAMC35")}
                <br />
                <br />
                {t("TATA:IOITAMC36")}
              </p>
            </div>
          </div>
        </div>

        <NumbersAPI />
        <div className="calCulator">
          <div className="container container-wrapper">
            <h2 className="mb-4">TATA PMS Calculator</h2>
            <CompoundInterest />
          </div>
        </div>
        <div className="container testimonials">
          <div className="row">
            <div className="col-lg-12">
              <h2>{t("TATA:IOITAMC36-A")}</h2>
              <div id="accordion">
                <h3>{t("TATA:IOITAMC37")}</h3>
                <p>{t("TATA:IOITAMC38")} </p>

                <h3>{t("TATA:IOITAMC39")}</h3>
                <p>{t("TATA:IOITAMC40")} </p>

                <h3>{t("TATA:IOITAMC41")}</h3>
                <p>
                  {t("TATA:IOITAMC42")}
                  <br />
                  {t("TATA:IOITAMC43")}
                  <br />
                  {t("TATA:IOITAMC44")}
                  <br />
                  {t("TATA:IOITAMC45")}
                  <br />
                  {t("TATA:IOITAMC46")}
                  <br />
                  {t("TATA:IOITAMC47")}
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default withTranslation()(pms)
